import { createRouter, createWebHistory } from "vue-router"

import pathNotfound from "../components/pathNotfound.vue"
import loginPage from "../components/LoginPage.vue"
import registerPage from "../components/RegisterPage.vue"
import homePage from "../components/HomePage.vue"
import masterSensor from "../components/MasterSensor.vue"
import hardwarePage from "../components/HardwarePage.vue"
import transaksiPage from "../components/TransaksiPage.vue"
import laporanPage from "../components/LaporanPage.vue"
import serverNotconected from "../components/ServerNotconected.vue"

const routes = [
    { path: '/login', name: 'login', component: loginPage, meta: { isAuth: false } },
    { path: '/register', name: 'register', component: registerPage, meta: { isAuth: false } },
    { path: '/', name: 'home', component: homePage, meta: { isAuth: true } },
    { path: '/master-sensor', name: 'mastersensor', component: masterSensor, meta: { isAuth: true } },
    { path: '/hardware', name: 'hardware', component: hardwarePage, meta: { isAuth: true } },
    { path: '/transaksi', name: 'transaksi', component: transaksiPage, meta: { isAuth: true } },
    { path: '/laporan', name: 'laporan', component: laporanPage, meta: { isAuth: true } },
    { path: '/:pathMach(.*)*', component: pathNotfound, meta: { isAuth: false } },
    { path: '/erro-503', component: serverNotconected, meta: { isAuth: false } }
]

const router = createRouter({
    routes,
    history: createWebHistory()
})

router.beforeEach((to, from, next) => {
    if (to.meta.isAuth && !localStorage.getItem('token')) {
        next('/login')
    } else {
        next()
    }
})

export default router