<template>
    <div class="h-screen">
  <form @submit.prevent="register" class="w-96 mx-auto pt-16">
    <div class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4 flex flex-col">
      <div class="mb-4 w-full text-center">
        <h2 class="font-semibold text-slate-700 text-3xl">Register Form</h2>
        <p v-if="error" class="text-red-700 mt-6 py-2 px-4 font-semibold bg-red-100 rounded-lg">{{ error }}</p>
      </div>
        <div class="mb-4">
          <label class="block text-grey-darker text-sm font-bold mb-2" for="nama">
            Nama lengkap
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker" v-model="form.name" id="nama" type="text" placeholder="Nama lengkap">
          <span class="text-red-700"><i id="msg_nama"></i></span>
        </div>
        <div class="mb-4">
          <label class="block text-grey-darker text-sm font-bold mb-2" for="email">
            Email
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker" v-model="form.email" id="email" type="text" placeholder="Email">
          <span class="text-red-700"><i id="msg_email"></i></span>
        </div>
        <div class="mb-6">
          <label class="block text-grey-darker text-sm font-bold mb-2" for="password">
            Password
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker mb-3" v-model="form.password" id="password" type="password" placeholder="******************">
          <span class="text-red-700"><i id="msg_password"></i></span>
        </div>
        <div class="flex items-center justify-between">
          <button id="submit" class="bg-blue-500 hover:bg-blue-dark text-white font-bold py-2 px-4 rounded" type="submit">
            Register
          </button>
          <router-link to="/login">Login</router-link>
        </div>
    </div>
  </form>
  </div>
</template>
<script setup>
import axios from 'axios'
import jquery from 'jquery'
import { reactive, ref } from 'vue'
import {useRouter} from 'vue-router'
import Swal from 'sweetalert2'

const router = useRouter() 

let form = reactive({
  name:'',
  email: '',
  password: ''
})

let error = ref('')

const register = async()=>{
  await axios.post('http://localhost:8000/api/register',form)
    .then(response =>{
      if(response.data.success){
        Swal.fire({
            toast: true,
            position: 'top-end',
            icon: 'success',
            title: 'Registrasi berhasil, silahkan login',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1500
        }).then((result) => {
            if (result.dismiss === Swal.DismissReason.timer) {
                router.push('/login')
            }
        })
      }else{
        if (response.data.message.name) {
            jquery('#nama').addClass('border-red-700')
            jquery('#msg_nama').html(response.data.message.name)
        } else {
            jquery('#nama').removeClass('border-red-700')
            jquery('#msg_nama').html('')
        }

        if (response.data.message.email) {
            jquery('#email').addClass('border-red-700')
            jquery('#msg_email').html(response.data.message.email)
        } else {
            jquery('#email').removeClass('border-red-700')
            jquery('#msg_email').html('')
        }

        if (response.data.message.password) {
            jquery('#password').addClass('border-red-700')
            jquery('#msg_password').html(response.data.message.password)
        } else {
            jquery('#password').removeClass('border-red-700')
            jquery('#msg_password').html('')
        }

      }
    })
}
</script>