<script>
import NavBar from '../NavBar.vue';
import SideBar from '../SideBar.vue';
import Datepicker from 'vue3-datepicker'
import jquery from 'jquery';
import axios from 'axios';
import Swal from 'sweetalert2'
import exportFromJSON from "export-from-json";

export default {
  name: 'LaporanPage',
  data() {
    return {
      mulai: '',
      berakhir: '',
      hardware: '',
      loading: false,
      preload: false,
      modalsatu: false,
      modaldua: false,
      modaltiga: false,
      userData:'',
      datahardware: '',
      datahardwareDua: '',
      datahardwareTiga: '',
      datadetail: ''
    }
  },
  components: {
    NavBar,
    SideBar,
    Datepicker,
  },
  methods: {
    excel: function(){
      this.preload=true
      const formData = {
        mulai: jquery('#start').val(),
        berakhir: jquery('#end').val(),
        hardware: jquery('#hardware').val()
      }

      axios.post('/laporan/download-excel', formData)
      .then(response => {
        if(response.status == 200){
          const data = response.data.message;
          const fileName = "Excel Laporan Hardware";
          const exportType = exportFromJSON.types.csv;

          if (data) exportFromJSON({ data, fileName, exportType }); 
          // console.log(response.data)
        }
      }).finally(() => this.preload = false)
    },

    pdf: function(){
      this.preload=true

      const formData = {
        mulai: jquery('#start').val(),
        berakhir: jquery('#end').val(),
        hardware: jquery('#hardware').val()
      }

      axios.post('/laporan/download-pdf', formData,{ headers: {
                'Content-Type': 'multipart/form-data',
            },
                'responseType': 'blob' 
            })
            .then(response=>{
                if(response.status == 200){
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', 'Laporan Hardware.pdf');
                    document.body.appendChild(link);
                    link.click();
                }
            })
            .catch(error=>{
                console.log(error);
            }).finally(() => this.preload = false)
    },

    closeModal: function(){
      this.modalsatu= false
      this.modaldua= false
      this.modaltiga= false
    },

    submit: function () {
      const senddata = {
        mulai: this.mulai,
        berakhir: this.berakhir,
        hardware: this.hardware
      }

      this.loading = true

      axios.post('/laporan', senddata
      ).then(response => {
        if (response.data.success == true) {
          this.dataCallback(response.data)
        } else {
          Swal.fire({
            toast: true,
            position: 'top-end',
            icon: 'warning',
            title: response.data.message,
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1500
          })
        }
      }).catch(error => {
        if(error){
          Swal.fire({
            toast: true,
            position: 'top-end',
            icon: 'warning',
            title: 'Ups someting wrong',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1500
          }) 
        }
      }).finally(() => this.loading = false)
    },

    dataCallback(datas){
      this.datadetail = datas.detail

      if (datas.type == '4001') {
        this.modalsatu = true
        this.datahardware = datas.message
      }

      if (datas.type == '4002') {
        this.modaldua = true
        this.datahardwareDua = datas.message
      }

      if (datas.type == '4003') {
        this.modaltiga = true
        this.datahardwareTiga = datas.message
      }
    },
  },
}
</script>