<script setup>
import axios from 'axios'
import { reactive, ref } from 'vue'
import {useRouter} from 'vue-router'

const router = useRouter() 

let form = reactive({
  email: '',
  password: ''
})

let error = ref('')

const login = async()=>{
  await axios.post('http://localhost:8000/api/login',form)
    .then(response =>{
      if(response.data.success){
        localStorage.setItem('token', response.data.data_token.token)
        router.push('/')
      }else{
        error.value = response.data.message
      }
    })
}
</script>
<template>
  <div class="h-screen">
  <form @submit.prevent="login" class="w-96 mx-auto pt-16">
    <div class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4 flex flex-col">
      <div class="mb-4 w-full text-center">
        <h2 class="font-semibold text-slate-700 text-3xl">Login Form</h2>
        <p v-if="error" class="text-red-700 mt-6 py-2 px-4 font-semibold bg-red-100 rounded-lg">{{ error }}</p>
      </div>
        <div class="mb-4">
          <label class="block text-grey-darker text-sm font-bold mb-2" for="email">
            Email
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker" v-model="form.email" id="email" type="text" placeholder="email@mail.com">
        </div>
        <div class="mb-6">
          <label class="block text-grey-darker text-sm font-bold mb-2" for="password">
            Password
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker mb-3" v-model="form.password" id="password" type="password" placeholder="******************">
        </div>
        <div class="flex items-center justify-between">
          <button id="submit" class="bg-blue-500 hover:bg-blue-dark text-white font-bold py-2 px-4 rounded" type="submit">
            Sign In
          </button>
          <router-link to="/register">Register</router-link>
        </div>
    </div>
  </form>
  </div>
</template>